function generateHTML() {
  const inputs = document.querySelectorAll('input');

  function formInputs() {
    const inputsArray = [];
    for (let [i, input] of inputs.entries()) {
      inputsArray.push(input.value);
    }
    return inputsArray;
  }

  let html =
    `<body style="margin:0;padding:0;box-sizing:border-box;margin-top:3em"><div style="margin:0;padding:0;box-sizing:border-box"><a href="https://www.deronners.com/" target="_blank" title="studio de Ronners" rel="noopener" style="color:#000;text-decoration:none"><img src="https://www.deronners.com/wp-content/uploads/2023/01/sdro-email_logo.png" alt="studio de ronners logo" width="60px" style="margin:0;padding:0;box-sizing:border-box;border:1px solid #000;border-radius:100%;margin-right:1em"></a></div><div style="margin:0;padding:0;box-sizing:border-box;width:100%;margin-left:60px;padding-left:1em;margin-top:-60px"><p style="margin:0;padding:0;box-sizing:border-box;font-family:Arial,Helvetica,sans-serif;margin-bottom:1em"><strong>${formInputs()[0]}</strong><br>${formInputs()[1]}</p><p style="margin:0;padding:0;box-sizing:border-box;font-family:Arial,Helvetica,sans-serif;margin-bottom:1em">M: ${formInputs()[2]}<br>Available ${formInputs()[3]}â€“${formInputs()[4]}</p><p style="margin:0;padding:0;box-sizing:border-box;font-family:Arial,Helvetica,sans-serif;margin-bottom:1em"><a href="https://www.deronners.com/" target="_blank" title="studio de Ronners" rel="noopener" style="color:#000;text-decoration:none"><strong>studio de Ronners</strong></a><br>Rotterdam | Groningen | Antwerp</p><p style="margin:0;padding:0;box-sizing:border-box;font-family:Arial,Helvetica,sans-serif;margin-bottom:1em">Remarkable, Bold and Relevant<br><a href="https://www.instagram.com/deronners/" target="_blank" title="studio de Ronners | Instagram" rel="noopener" style="color:#000;text-decoration:underline">Instagram</a> | <a href="https://www.linkedin.com/company/studioderonners" target="_blank" title="studio de Ronners | LinkedIn" rel="noopener" style="color:#000;text-decoration:underline">LinkedIn</a> | <a href="https://vimeo.com/deronners" target="_blank" title="studio de Ronners | Vimeo" rel="noopener" style="color:#000;text-decoration:underline">Vimeo</a></p></div></body>`

  return html;
}

const GENERATE = document.querySelector('#btn-generate');
GENERATE.addEventListener("click", function (e) {
  e.preventDefault();
  document.querySelector('main p').innerHTML = `<xmp class="whitespace-pre-wrap m-0">${generateHTML()}</xmp>`;
})